<template>
    <div class="famousTeacherInfo">
      <div class="header">
        <span @click="back" class="back">
            <img src="../assets/img/back.png">
        </span>
        <span class="usersTitle">名师堂</span>
        <span class="back"></span>
      </div>
      <div class="teacherInfo">
          <span class="headPic">
              <img :src="teacher.headPic">
          </span>
          <span class="details">
              <span class="name">{{ teacher.name }}</span>
              <br>
              <span class="introduce twoLine" ref="introduce" @click="showAll">{{ teacher.introduce }}</span>
          </span>
      </div>
      <div class="border"></div>
      <div class="lessonSection">
          <div class="title">讲述课程({{ lessons.length }}套)</div>
          <ul class="lessons">
              <li class="lesson" v-for="(lesson, index) in lessons" :key="index" @click="showLessonList(index)">
                  <span class="lessonImg">
                      <img :src="lesson.img">
                  </span>
                  <span class="lessonInfo">
                      <span class="lessonTitle">{{ lesson.title }}</span>
                      <span class="space"></span>
                      <span class="lessonDetails">
                          <span class="members">{{ lesson.members }}人已学</span>
                          <span class="price">￥{{ lesson.price }}</span>
                      </span>
                  </span>
                  <span class="next">
                      <img src="../assets/img/next.png">
                  </span>
              </li>
          </ul>
      </div>
    </div>
</template>

<script>
export default {
  name: 'famousTeacherInfo',
  data () {
    return {
      teacher: {headPic: require('../assets/img/headTest.png'), name: '康伟', introduce: '国务院总理李克强1月9日主持召开国务院常务会议，决定再推出一批针对小微企业的普惠性减税措施；部署加快发行和用好地方政府专项债券，支持在建工程及补短板项目建设并带动消费扩大'},
      lessons: [{
        img: require('../assets/img/test.png'), title: '太极拳五个经典动作的秘籍', members: 300, price: '40.00'
      }, {
        img: require('../assets/img/test.png'), title: '太极拳五个经典动作的秘籍', members: 300, price: '40.00'
      }, {
        img: require('../assets/img/test.png'), title: '太极拳五个经典动作的秘籍', members: 300, price: '40.00'
      }, {
        img: require('../assets/img/test.png'), title: '太极拳五个经典动作的秘籍', members: 300, price: '40.00'
      }]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    showAll () {
      this.$refs.introduce.classList.remove('twoLine')
    },
    showLessonList (index) {
      this.$router.push('/home/index/lessonList')
    }
  }
}
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    font-size: 0.48rem;
    background: #fff;
    z-index: 99;
    top: 0;
    left: 0;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back,
  .next {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .teacherInfo {
    margin-top: 1.5rem;
    display: flex;
    padding: 0.37333rem;
  }

  .headPic {
    display: flex;
    width: 1.6rem;
    height: 1.6rem;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
  }

  .headPic img {
    border-radius: 0.8rem;
  }

  .details {
    margin-left: 0.4rem;
    flex-grow: 1;
    margin-top: 0.2rem;
  }

  .name,
  .introduce {
    width: 100%;
    display: inline-block;
    text-align: start;
  }

  .name {
    color: #333333;
  }

  .introduce {
    font-size: 0.37333rem;
    margin-top: 0.2rem;
    color: #666666;
  }

  .twoLine {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  .border {
    width: 100%;
    height: 0.32rem;
    background: #f5f5f5;
  }

  .lessonSection {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 0.66667rem;
  }

  .title {
    text-align: start;
    font-size: 0.42667rem;
    margin-left: 0.21333rem;
    position: relative;
    z-index: -1;
  }

  .title::before {
    content: '';
    display: block;
    position: absolute;
    width: 0.05333rem;
    height: 100%;
    left: -0.21333rem;
    top: 0;
    background: #ffd200;
  }

  .lessons {
    list-style: none;
  }

  .lesson {
    display: flex;
    align-items: center;
    margin: 0.32rem 0;
  }

  .lessonImg {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3.2rem;
    height: 2.24rem;
  }

  .lessonImg img {
    border-radius: 0.15rem;
  }

  .lessonInfo {
    display: inline-block;
    flex-grow: 1;
    margin-left: 0.32rem;
  }

  .lessonTitle {
    font-size: 0.37333rem;
    color: #333333;
  }

  .lessonDetails span{
    font-size: 0.34667rem;
    color: #666666;
  }

  .lessonTitle,
  .lessonDetails {
    display: inline-block;
    width: 100%;
    text-align: start;
  }

  .lessonDetails {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .price {
    margin-right: 0.26667rem;
    color: #ff8309!important
  }

  .space {
    display: block;
    height: 0.4rem;
    width: 100%;
  }
</style>



